import JXObject from "jx/core/JXObject";
class YScaleAppear extends JXObject {
    constructor({}) {
        super();
    }
    run({ parent, item, onFinished = (e) => { }, onChanging = (e) => { }, typeTransition = "elasticOut" }) {
        createjs.Tween.get(item.view, { override: true })
            .to({ scaleY: 1 }, 500, createjs.Ease[typeTransition]) // item2 appears going to scaleY = 1
            .call((e) => onFinished(e), undefined, this)
            .addEventListener("change", (e) => onChanging({ target: item, currentTarget: this }));
    }
}
export default YScaleAppear;
